import { create } from 'zustand';

export const useGameStore = create((set) => ({
  state: 'wating', // 
  result: '', // 
  gamerecord: [], // 
  scorebarviewing: false,
  timer: 10, // 
  score: 0, // 
  rate: 0, // 
  setRate: (newRate) =>
    set((state) => ({
      rate: newRate,
    })),
  setRecording: (newRecording) =>
    set((state) => ({
      gamerecord: [...state.gamerecord, newRecording],
    })),
  setScore: (newScore) =>
    set((state) => ({
      score: state.score + newScore,
    })),
  setState: (newState) => {
    // 
    set((prev) => ({
      ...prev,
      state: newState,
      scorebarviewing: true,
      score: 0,
      rate: 0,
      timer: 10,
    }));
  },
  setScoreBarViewing: (newViewing) => {
    set({ scorebarviewing: newViewing });
  },
  setTimer: (newTimer) => {
    set({ timer: newTimer });
  },
  setEnd: (newEnd) => {
    set({ end: newEnd });
  },
}));

export const useOptionStore = create((set) => ({
  crosshair: 'dot', //  
  MeshCount: 4, //  
  MeshColor: 'blue', //  
  ColorChange: (newState) => {
    set((state) => ({
      MeshColor: newState,
    }));
  },
  ChangeCH: (newState) => {
    set((state) => ({
      crosshair: newState,
    }));
  },
  setMeshCount: (newState) => {
    set((state) => ({
      MeshCount: newState,
    }));
  },
}));
